@import url(https://fonts.googleapis.com/css?family=Alata&display=swap);
body {
  margin: 0;
  font-family: "Alata", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Alata", sans-serif;
}

.clearfix {
  position: relative;
}

.clearfix:after {
  content: "";
  display: block;
  clear: both;
  position: absolute;
}


/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
header,
div {
  box-sizing: border-box;
}

.comp-header {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 0;
  z-index: 8000;
  background-color: #fff;
  height: 72px;
  border-bottom: solid #000 2px;
  padding: 0 20px; }
  .comp-header .title-box {
    float: left;
    font-size: 20px; }
    .comp-header .title-box .nav-link {
      margin: 0 10px;
      display: inline-block;
      line-height: 70px;
      font-size: 20px; }
  .comp-header .nav-box {
    float: right; }
    .comp-header .nav-box .nav-link-box {
      display: inline-block; }
      .comp-header .nav-box .nav-link-box .nav-link {
        margin: 0 10px;
        display: inline-block;
        line-height: 70px;
        font-size: 20px; }

.home-comp .post-link-box {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center, center;
  position: relative; }
  .home-comp .post-link-box .post-link-content {
    opacity: 0.9;
    -webkit-transition: all 0.23s ease;
    transition: all 0.23s ease;
    cursor: pointer; }
  .home-comp .post-link-box:hover .post-link-content {
    opacity: 1;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    z-index: 2; }

.home-comp .post-image {
  width: 100%; }

.home-comp .my-masonry-grid {
  display: flex;
  margin-left: -30px;
  width: auto; }

.home-comp .my-masonry-grid_column {
  padding-left: 30px;
  background-clip: padding-box; }

.home-comp .my-masonry-grid_column > div {
  background: grey;
  margin-bottom: 30px; }




.content-wrap {
  margin-top: 72px; }
  .content-wrap .fade-enter {
    opacity: 0; }
  .content-wrap .fade-enter-active {
    opacity: 1;
    -webkit-transition: opacity 200ms;
    transition: opacity 200ms; }
  .content-wrap .fade-exit {
    opacity: 1; }
  .content-wrap .fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 200ms;
    transition: opacity 200ms; }

